<template>
  <el-table :data="serveList" class="custom-table">
    <el-table-column
      prop="combineServiceName"
      label="服务名称"
      align="center"
    ></el-table-column>
    <el-table-column prop="owned" label="总数量" align="center">
      <template #default="{ row }"> {{ row.owned }}{{ row.unit }} </template>
    </el-table-column>
    <el-table-column prop="used" label="已用数量" align="center">
      <template #default="{ row }"> {{ row.used }}{{ row.unit }} </template>
    </el-table-column>
    <el-table-column prop="residue" label="剩余数量" align="center">
      <template #default="{ row }"> {{ row.residue }}{{ row.unit }} </template>
    </el-table-column>
    <el-table-column
      prop="startTime"
      label="购买日期"
      align="center"
    ></el-table-column>
    <el-table-column
      prop="endTime"
      label="到期时间"
      align="center"
    ></el-table-column>
    <template #empty>
      <TableEmpty empty-text="暂无交易订单"></TableEmpty>
    </template>
  </el-table>
</template>
<script lang="ts" setup>
import { shallowRef } from "vue";
import { useVueRouter } from "@/assets/useHook";

import { customerByServeApi } from "@api/customer-management-api";
const { routeQuery } = useVueRouter();
const serveList = shallowRef<unknown[]>([]);
// 获取已购服务列表
const getServeList = () => {
  const params = {
    companyId: routeQuery.value.companyId,
  };
  customerByServeApi.getData(params).then((res) => {
    serveList.value = customerByServeApi.handleData(res.data);
  });
};
getServeList();
</script>
